import axios from 'axios';
export default {
  data() {
    return {
      root: 'my-online-portfolio',
      data: null,
      projects: [],
      loading: true
    };
  },

  watch: {
    channels() {
      console.log(this.channels);

      for (const channel of this.channels) {
        this.fetchChannel(channel);
      }
    }

  },
  computed: {
    channels() {
      if (this.data) {
        return this.data.filter(item => item.base_class === 'Channel');
      } else {
        return null;
      }
    }

  },
  methods: {
    fetchChannels(channelString) {
      this.error = null;
      axios.get(`https://api.are.na/v2/channels/${channelString}`).then(response => {
        if (response) {
          this.data = response.data.contents;
        } else {
          console.log('error');
        }
      });
    },

    fetchChannel(channel) {
      this.error = null;
      axios.get(`https://api.are.na/v2/channels/${channel.slug}`).then(response => {
        if (response) {
          this.projects.push({
            meta: channel,
            content: response.data.contents
          });
          console.log(response.data.contents);
        } else {
          console.log('error');
        }
      });
    }

  },

  mounted() {
    console.log('MOUNTED');
    this.fetchChannels(this.root);
  }

};