export default {
  props: {
    project: Object
  },
  computed: {
    texts() {
      return this.project.content.filter(item => item.class === 'Text');
    },

    links() {
      return this.project.content.filter(item => item.class === 'Link');
    },

    images() {
      return this.project.content.filter(item => item.class === 'Image');
    }

  }
};